/**
 * 	Utility functions for various parts of the extension.
 *
 * 	@author Daniel Moffat
 */

var Config = require('../config.development.js');
var getBrowserName = require('./get-browser-name.js');
var getMobileOs = require('./get-mobile-os.js');

/**
 * @return true if the given object has all of the properties specified in the props array.
 */
function hasProperties(object, props) {
	if(!props || !props.length) {
		return true;
	}
	props.forEach(function(prop) {
		if(!object.hasOwnProperty(prop)) {
			return false;
		}
	});
	return true;
}

/**
 * @return True if the given string equals "true"
 */
function toBoolean(str) {
	return str === 'true'
}

/**
 * @return True if the given input is undefined. (Declared, but not defined.)
 */
function isUndefined(input) {
	return input === undefined;
}

/**
 * @return True if the given 'obj' is an array.
 */
function isArray(obj) {
	return Object.prototype.toString.call(obj) === '[object Array]';
}

/**
 * @return True if the given 'obj' is a string.
 */
function isString(obj) {
	return Object.prototype.toString.call(obj) === '[object String]';
}

/**
 *	Helper method for iterating NodeList objects (or other objects that pretend to be arrays).
 *	In Chrome NodeList.prototype.forEach exists, but not in other browsers.
 *
 * 	Invokes 'callback' for each item in 'arr'.
 *
 */
function forEach(arr, callback) {
	for (var i = 0; i < arr.length; i++) {
		callback(arr[i]);
	}
}

/**
 *  @return The first parent node which has the given 'className', or null if no parent is found.
 */
function findParentWithClassName(currElement, className) {
	var element = currElement;
	while(!element.classList.contains(className)) {
		element = element.parentNode;
		if(element === null) { // No parent node, can't go any further.
			return null;
		}
	}
	return element;
}

/**
 * 	Given a list of stores, will set the 'isFavourite' property on each store if they are also in
 * 	'userFavourites' (which is also a list of stores.).
 */
function markFavourites(stores, userFavourites) {
	var stores = stores || [];
	var userFavourites = userFavourites || [];
	var favouriteStoreIds = userFavourites.map(function(favourite) { return favourite.id } );
	stores.forEach(function(store) {
		store.isFavourite = favouriteStoreIds.includes(store.id);
	});
	return stores;
}

/**
 *	Will invoke 'callback' when the document is ready.
 *
 * 	This might be invoked when the document is still in a 'loading' state, so we need to check the ready state
 *  and either bind the callback to the 'DOMContentLoaded' event or just execute it straight away.
 */
function whenDocumentReady(callback) {
	if(document.readyState === 'loading') { // Document not loaded yet.
		document.addEventListener('DOMContentLoaded', function() {
			callback();
		});
		return;
	}

	// Document already interactive
	callback();
}

/**
 * @return An error object, with the given type as the 'type' property.
 */
function createError(type) {
	var err = new Error();
	err.type = type;
	return err;
}

/**
 * @return True if the given 'url' matches that of a SERP (e.g. Google, Bing, Yahoo etc.)
 */
function isSerp(url) {
	if(!url) return false;

	var patterns = [
		/^(http|https):\/\/www.google.(co.uk|com)/g,
		/^(http|https):\/\/www.bing.com\/search/g,
		/^(http|https):\/\/uk.search.yahoo.com\/search/g
	];

	for (var i = 0; i < patterns.length; i++) {
		if(url.match(patterns[i])) {
			return true;
		}
	}

	return false;
}

/**
 * @return True if the given url is a clickthrough URL
 */
function isClickthrough(url) {
	if(!url) return false;
	return url.indexOf('statistics/gayl/clickthrough') > 0;
}

/**
 * @return True if the given url is the GAYL website.
 */
function isGiveasyouLive(url) {
	var re = /https?:\/\/(.*)giveasyoulive.(com|uk)/g;
	return re.test(url);
}

/**
 * @return True if the given url is a GAYL email URL.
 */
function isGiveasyouLiveEmail(url) {
	if(!url) return false;
	return url.indexOf('giveasyoulive-emails.com') > 0;
}

/**
 * @return True if the given URL is a retailer page, which is assumed
 * if !isGiveasyouLive and !isSerp, we don't have a more reliable (and quick)
 * method to test if a URL is a retailers.
 */
function isRetailerPage(url) {
	return !isGiveasyouLive(url) && !isSerp(url);
}

/**
 *	Intialises a content script.
 *
 *  If the browser is Safari, the content script will only be initialised if condition is true.
 *  For all other browsers, the content script will be intialised straight away.
 *
 *  'condition' is needed because Safari doesn't support the whitelist/blocklist in their manifest file yet.
 */
function contentScriptInitialise(contentScriptConstructor, condition) {
	if(window.top === window) {
		if(getBrowserName() === "Safari") {
			if(condition) {
				new contentScriptConstructor();
			}
		} else {
			new contentScriptConstructor();
		}
	}
}

/**
 * Helper function for creating DOM nodes in a non-verbose way.
 * @param  tagName, the name of the tag to create
 * @param  classNames, a single class name, or an array of class names to add to the node
 * @param  textContent The text content of the node.
 *
 * e.g.
 * dom('div', ['foo', 'bar'], 'Hello world!')
 * => <div class="foo bar">Hello world!</div>
 */
function dom(tagName, classNames, textContent) {
	var el = document.createElement(tagName);
	if(isArray(classNames)) {
		classNames.forEach(function(className) {
			if(className) {
				el.classList.add(className);
			}
		});
	} else {
		if(classNames) {
			el.classList.add(classNames);
		}
	}

	if(textContent) {
		el.textContent = textContent;
	}

	return el;
}

/**
 * Helper function for creating <svg> elements (and their children, e.g. <path>, <circle>...).
 * You cannot just use dom for this. The resulting elements don't get rendered by the browser.
 */
function svgDom(tagName, classNames) {
	var el = document.createElementNS('http://www.w3.org/2000/svg', tagName);
	if(!classNames) return el;

	if(isArray(classNames)) {
		classNames.forEach(function(className) {
			if(className) {
				el.classList.add(className);
			}
		})
	} else {
		el.classList.add(className);
	}
	return el;
}

/**
 * Helper function for creating <img> elements
 * @param  src The image source
 * @param  alt The alt text
 */
function img(src, alt, classNames) {
	var i = dom('img', classNames);
	i.src = src;
	i.alt = alt;
	return i;
}

function removeChildren(node) {
	// Make sure we have a valid node reference and it has children
	if(!node || !node.hasChildNodes()) {
		return;
	}

	while(node.firstChild) {
		node.removeChild(node.firstChild);
	}
}

/**
 * Helper function for creating text nodes.
 */
function text(text) {
	return document.createTextNode(text);
}

/**
 * @return True if the new version is a major version upgrade from the previous version. A major
 * version upgrade is when any of the first two version numbers are greater than the previous version
 *
 * e.g:
 * isMajorVersionUpdate("6.0.0.0", "7.0.0.0")
 * => true
 *
 * isMajorVersionUpdate("6.0.0.0", "6.1.0.0")
 * => true
 *
 * isMajorVersionUpdate("6.0.0.0", "6.0.1.0")
 * => false
 */
function isMajorVersionUpdate(previousVersion, newVersion) {
	if(!newVersion) { return false; }
	if(!previousVersion && newVersion) { return true; }
	var previousVersionParts = previousVersion.split(".");
	var newVersionParts = newVersion.split(".");

	for(var i = 0; i < previousVersionParts.length; i++) {
		if(newVersionParts[i] > previousVersionParts[i]) {
			return true;
		}

		// Only check the first two numbers, the last two are minor versions
		if(i === 1) {
			return false;
		}
	}
}

/**
 * @return UNIX timestamp for the current date.
 */
function getTimestamp() {
	return Date.now();
}

/**
 * @return The current day and year joined with a '-', e.g. For 1st Jan 2018, you'd get '1-2018'
 */
function getDayAndYear() {
	var d = new Date();

	return (d.getDay() + 1) + '-' + (d.getYear() + 1900);
}

/**
 * Given an object, will return its string representation. Doesn't work very well if the value is an object / array.
 * e.g. logObject({foo: 'bar', baz: 'boo'})
 * => 'foo:bar,baz:boo'
 */
function logObject(obj) {
	if(!obj) return '';
	var out = '';

	for(var key in obj) {
		if(obj.hasOwnProperty(key)) {
			out += (key + ':' + obj[key] + ',')
		}
	}

	return out;
}

/**
 * Given an array, will return last item
 * @return The last item, or null if the given array is not an actual array or doesn't
 * contain any items.
 */
function getLastItem(arr) {
	if(!isArray(arr)) {
		return null;
	}

	if(arr.length == 0) {
		return null;
	}

	return arr[arr.length - 1];
}

/**
 * @param path Path to the resource on the GAYL website. A leading slash will be added if not already present.
 * @return The absolute URL to the path
 * e.g.	getGaylUrl('/foo/bar')
 * => 'https://www.giveasyoulive.com/foo/bar' for prod
 * => 'https://www.giveasyoulive.uk/foo/bar' for test
 */
function getGaylUrl(path) {
	if(!path) return Config.gaylUrl;
	var hasLeadingSlash = path.indexOf('/') === 0;

	return Config.gaylUrl + (hasLeadingSlash ? '' : '/') + path;
}

/**
 * Inserts the given newNode after the given referenceNode
 */
function insertAfter(newNode, referenceNode) {
    referenceNode.parentNode.insertBefore(newNode, referenceNode.nextSibling);
}

// Given a cause object, will add a "truncatedName" property
function truncateCauseName(cause, limit) {
	var len = cause.name.length;
	var suffix = '...';

	if(len < limit) {
		cause.truncatedName = cause.name;
	} else {
		cause.truncatedName = cause.name.substring(0, limit - suffix.length) + suffix;
	}

	return cause;
}

function isAndroidApp() {
	return getBrowserName() === 'Firefox' && getMobileOs() === 'Android';
}

module.exports = {
	contentScriptInitialise,
	createError,
	dom,
	findParentWithClassName,
	forEach,
	getTimestamp,
	getDayAndYear,
	getGaylUrl,
	getLastItem,
	hasProperties,
	img,
	insertAfter,
	isAndroidApp,
	isArray,
	isClickthrough,
	isGiveasyouLive,
	isGiveasyouLiveEmail,
	isMajorVersionUpdate,
	isSerp,
	isString,
	isRetailerPage,
	isUndefined,
	logObject,
	markFavourites,
	removeChildren,
	text,
	toBoolean,
	truncateCauseName,
	svgDom,
	whenDocumentReady
};