/**
 * 	Detects which browser the extension is currently running in.
 *
 * 	Code is pretty much directly lifted from the 'bowser' lib: https://github.com/lancedikson/bowser/blob/master/src/bowser.js
 *
 * 	@author Daniel Moffat
 */

function getBrowserName(userAgentStr) {
	var ua = userAgentStr || navigator.userAgent;

	if(/edg([ea]|ios)/i.test(ua)) {
		return 'Edge';
	} else if(/chrome|crios|crmo/i.test(ua)) {
		return 'Chrome';
	} else if(/safari|applewebkit/i.test(ua)) {
		return 'Safari';
	} else if(/firefox|iceweasel|fxios/i.test(ua)) {
		return 'Firefox';
	} else {
		// No match
		return null;
	}
}

module.exports = getBrowserName;