/**
 * 	Template for the Donation Reminder prompt on the retailer page (the thing that asks the user whether they want
 * 	 to turn the bar off or not.)
 *
 *  Model:
 *  {
		causeName: <name of users current cause>,
		storeName: <name of the current store>,
		maxStoreRate: <maximum rate the current store gives>,
		newCustomersOnly: <whether this retailer only pays commission for new customers>
 	}
 	If a user is logged out 'causeName' will be undefined.
 *
 * 	@author Daniel Moffat
 */

var { dom, text } = require('../../shared/util.js');
var Messages = require('../../shared/messages.js');
var rightArrowIcon = require('../icons/right-arrow.js');
var runtime = require('../../web-ext/runtime.js');

function confirmationPrompt(data) {
	var causeName = data.causeName || 'your charity';
	var wrapper = dom('div', ['reset-this', 'confirmation-dialog']);
	wrapper.setAttribute('id', 'gayl-donation-reminder-root');

	// Logo
	var logo = dom('img', ['confirmation-dialog__logo', 'reset-this']);
	logo.src = runtime.getURL('/images/online.svg');

	// Question
	var question = questionMessage(Messages.STORE_CONFIRMATION_DIALOG_QUESTION, causeName);

	// Earning statement
	var earning = dom('p', ['confirmation-dialog__earn', 'reset-this'])
	var message = data.newCustomersOnly ? 
		Messages.STORE_CONFIRMATION_DIALOG_EARN_NEW_CUSTOMERS : 
		Messages.STORE_CONFIRMATION_DIALOG_EARN;
	// Replace the dynamic parts
	message = message
		.replace('$$storeName$$', data.storeName)
		.replace('$$maxStoreRate$$', data.maxStoreRate);
	earning.appendChild(text(message));

	// Confirm button
	var confirm = dom('a', ['btn', 'btn-primary', 'btn-lg', 'js-retailer-confirm', 'reset-this'], Messages.STORE_CONFIRMATION_DIALOG_YES);
	confirm.appendChild(text(' '));
	confirm.appendChild(rightArrowIcon());

	// Close button
	var close = dom('img', ['js-retailer-decline', 'confirmation-dialog__close', 'reset-this']);
	close.src = runtime.getURL('/images/close-icon.svg');

	// Assemble
	wrapper.appendChild(logo);
	wrapper.appendChild(question);
	wrapper.appendChild(earning);
	wrapper.appendChild(confirm);
	wrapper.appendChild(close);

	return wrapper;
}

// Given a message, and a cause name, will return the message, with the causeName wrapped in a <strong> tag.
function questionMessage(message, causeName) {
	var wrapper = dom('p', ['confirmation-dialog__question', 'reset-this']);
	var parts = message.split('$$causeName$$');
	var startText = parts[0];
	var endText = parts[1];
	if(startText) {
		wrapper.appendChild(text(startText));
	}
	wrapper.appendChild(dom('strong', ['reset-this'], causeName));
	if(endText) {
		wrapper.appendChild(text(endText))
	}

	return wrapper;
}

module.exports = confirmationPrompt;