/**
 * 	Detects which mobile operating system is used
 *
 * 	@author Gareth Evans
 */

function getMobileOS(userAgentStr) {
	var ua = userAgentStr || navigator.userAgent;

    if(/windows phone/i.test(ua)) {
		return 'Windows';
	} else if(/android/i.test(ua)) {
		return 'Android';
	} else if(/iPad|iPhone|iPod/i.test(ua)) {
		return 'iOS';
	} else {
		// No match
		return null;
	}
}

module.exports = getMobileOS;