/**
 * 	Model for messages that are sent between extension components (i.e. background page, content scripts, etc).
 *
 * 	@author Daniel Moffat
 */

/**
 * Defines the various message subjects and what they mean.
 */
var MessageSubject = {
	/**
	 * Background page => Content script
	 */

	// Inject the confirmation UI into the retailer page
	RETAILER_CONFIRMATION: 'RETAILER_CONFIRMATION',

	// Inject the thank you UI into the retailer page
	RETAILER_THANKYOU: 'RETAILER_THANKYOU',

	// Hide the confirmation UI
	HIDE_CONFIRMATION: 'HIDE_CONFIRMATION',

	// Remove all injected dialogs from the page
	REMOVE_ALL: 'REMOVE_ALL',

	// Contains the SERP rules
	GET_SERP_RULES_RESPONSE: 'GET_SERP_RULES_RESPONSE',

	// Contains the extracted SERP URLs and whether they are supported on our site or not.
	CHECK_SERP_URLS_RESPONSE: 'CHECK_SERP_URLS_RESPONSE',

	/**
	 *	Content script => Background page
	 */

	// User wants to activate this retailer
	RETAILER_CONFIRM: 'RETAILER_CONFIRM',

	// User doesn't want to activate this retailer
	RETAILER_REJECT: 'RETAILER_REJECT',

	// Request SERP rules
	GET_SERP_RULES: 'GET_SERP_RULES',

	// Check extracted SERP URLs
	CHECK_SERP_URLS: 'CHECK_SERP_URLS'
};

function Message(subject, payload) {
	var payload = payload || {};
	// Only allows certain messages that we're certain can be handled.
	if(this.isValidSubject(subject)) {
		// Tells the other end what this message is for.
		this.subject = subject;
	}

	// Some data sent with this message
	this.payload = payload;
}

Message.prototype.isValidSubject = function(subject) {
	if(!MessageSubject[subject]) {
		throw new Error("Unknown message subject: " + subject);
	}
	return true;
}

module.exports = {
	Message,
	MessageSubject
};