/**
 * 	Extension messages.
 *
 * 	These are generally added to the DOM using element.textContent = message, so you can't use
 * 	HTML in them - if you need to use HTML, code will need to written for the template the text is used in.
 *
 * 	Some of them also have placeholders available, e.g. $$causeName$$
 *
 *  @author Daniel Moffat
 */

