	POPUP_NO_SIMILAR_STORES: 'No similar stores were found for this retailer.',
	// $$query$$ will be replaced with the users search query and wrapped in a <strong> tag.
	POPUP_NO_SEARCH_RESULTS: 'No results found for $$query$$.',

	/**
	 * Popup error messages
	 */

	ERROR_TIMEOUT_POPUP: 'An error occured, please close, and re-open this popup. If the problem persists, please let us know @ help@everyclick.com',
