/**
 * 	Template for the Donation Reminder thank you message on the retailer page. It thanks them for activating.
 *
 *  Model:
 *  {
		causeName: <name of users current cause>,
		maxStoreRate: <maximum rate the current store gives>,
		newCustomersOnly: <whether this retailer only pays commission for new customers>
 	}
 	If a user is logged out, causeName will be undefined.
 *
 * 	@author Daniel Moffat
 */

var { dom, text } = require('../../shared/util.js');
var Messages = require('../../shared/messages.js');
var rightArrowIcon = require('../icons/right-arrow.js');
var runtime = require('../../web-ext/runtime.js');

// Replaces placeholders inside 'message' with the data in the next 2 arguments and wraps them in <strong> tags.
function thankYouMessage(message, causeName, maxStoreRate) {
	// If more placeholders are added, we could write a method to replace arbitrary placeholders in strings (and wrap them in tags)...this is
	// a bit gross.
	var wrapper = dom('p', ['thank-you-dialog__message', 'reset-this']);

	var parts = message.split('$$maxStoreRate$$')
	var startText = parts[0];
	wrapper.appendChild(text(startText));
	wrapper.appendChild(dom('strong', ['reset-this'], maxStoreRate));

	var endText = parts[1]; // This contains the second placeholder
	parts = endText.split('$$causeName$$');
	startText = parts[0];
	endText = parts[1];
	wrapper.appendChild(text(startText));
	wrapper.appendChild(dom('strong', ['reset-this'], causeName));

	if(endText) {
		wrapper.appendChild(text(endText));
	}
	return wrapper;
}

function thankYou(data) {
	var causeName = data.causeName || 'your charity';
	var wrapper = dom('div', ['thank-you-dialog', 'reset-this']);
	wrapper.setAttribute('id', 'gayl-donation-reminder-root');

	// Top part
	var logo = dom('img', ['thank-you-dialog__logo', 'reset-this']);
	logo.src = runtime.getURL('/images/online-lw.svg');
	var head = dom('p', ['thank-you-dialog__message', 'reset-this']);
	head.appendChild(dom('strong', ['reset-this'], Messages.STORE_THANK_YOU_INTRO));

	// Body
	var bodyMessage = data.newCustomersOnly ? Messages.STORE_THANK_YOU_BODY_NEW_CUSTOMERS : Messages.STORE_THANK_YOU_BODY;
	var body = thankYouMessage(bodyMessage, causeName, data.maxStoreRate);
	var okButton = dom('a', ['thank-you-dialog__action', 'btn', 'btn-default', 'btn-lg', 'js-thankyou-ok', 'reset-this'], Messages.STORE_THANK_YOU_OK);
	okButton.appendChild(text(' '));
	okButton.appendChild(rightArrowIcon());

	wrapper.appendChild(logo);
	wrapper.appendChild(head);
	wrapper.appendChild(body);
	wrapper.appendChild(okButton);

	return wrapper;
}

module.exports = thankYou;