/**
 * 	Content script for retailer pages.
 *
 * 	All actions this script performs are based on messages receieved by the background script.
 *
 * 	If the user is not logged in, none of the messages will ever be sent from the background script, meaning
 * 	you can assume the user is logged in and all required data is available.
 *
 * 	@author Daniel Moffat.
 */

var styles = require('./styles/less/retailer.less');

var { contentScriptInitialise, isRetailerPage, whenDocumentReady } = require('./lib/shared/util.js');
var { Message, MessageSubject }  = require('./lib/shared/message.js');
var Config = require('./lib/config.development.js');
var confirmationTemplate = require('./lib/templates/retailer/confirmation-prompt.js');
var ExtensionSource = require('./lib/shared/extension-source.js');
var runtime = require('./lib/web-ext/runtime.js');
var thankYouTemplate = require('./lib/templates/retailer/thank-you.js');

var CONFIRMATION_DIALOG_SELECTOR = '#gayl-donation-reminder-root.confirmation-dialog';
var INJECTED_SELECTOR = '#gayl-donation-reminder-root';
var THANK_YOU_SELECTOR = '#gayl-donation-reminder-root.thank-you-dialog';

class RetailerContentScript {
	constructor() {
		this.autoInjectedThankYou = false;
		this.bindEvents();

		// Support injecting dialogs via URL parameter - useful for development as you don't need to click "Yes" to see
		// the "Thank you" message
		var autoInjectConfirmation = window.location.href.indexOf('inject=confirmation') > -1;
		var autoInjectThankyou = window.location.href.indexOf('inject=thankyou') > -1;

		if(autoInjectConfirmation) {
			this.injectConfirmationDialog(
				{name: 'Totally not Starlight'},
				{ max: 'Â£10.00', name: 'Amazon', newCustomersOnly: true}
			);
		}

		if(autoInjectThankyou) {
			this.autoInjectedThankYou = true;
			this.injectThankYou({name: 'Totally not Starlight'}, {max: '100%', newCustomersOnly: true});
		}
	}

	// Bind events handlers for non-document events
	bindEvents() {
		runtime.onMessage.addListener(this.onMessage.bind(this));
	}

	// Bind event handlers for the confirmation dialog
	bindConfirmationDialogEvents() {
		this.ui = {
			confirmButton: document.querySelector('.js-retailer-confirm'),
			declineButton: document.querySelector('.js-retailer-decline'),
		};

		this.ui.confirmButton.addEventListener('click', this.onConfirmation.bind(this));
		this.ui.declineButton.addEventListener('click', this.onDecline.bind(this));
	}

	// Bind event handlers for the thank you message
	bindThankYouEventHandlers() {
		this.ui = {
			okButton: document.querySelector('.js-thankyou-ok')
		};

		this.ui.okButton.addEventListener('click', this.onClickOk.bind(this));
	}

	// Fired when a message is received from the background script.
	onMessage(message, sender, sendResponse) {
		var { payload, subject } = message;
		switch(subject) {
			case MessageSubject.RETAILER_CONFIRMATION:
				this.injectConfirmationDialog(payload.cause, payload.store);
				break;
			case MessageSubject.RETAILER_THANKYOU:
				this.injectThankYou(payload.cause, payload.store);
				break;
			case MessageSubject.HIDE_CONFIRMATION:
				this.removeConfirmationDialog();
				break;
			case MessageSubject.REMOVE_ALL:
				this.removeAllDialogs();
				break;
			default:
				console.warn('Unable to handle event - ' + subject);
				break;
		}

		return true;
	}

	onClickOk(e) {
		this.hideThankYou();
	}

	// Remove all injected dialogs on the page
	removeAllDialogs() {
		removeNode(INJECTED_SELECTOR);
	}

	// Injects our UI into the retailer's website.
	// @param cause The cause object for the current user. If the user is logged out, cause will be NULL.
	// @param store The store object for the current store
	injectConfirmationDialog(cause, store) {
		var self = this;
		whenDocumentReady(function() {
			if(self.isInjected()) { // Prevent duplicate injections.
				return;
			}
			var template = confirmationTemplate({
				causeName: (cause && cause.name) || null, // todo: might need to truncate this a little bit, or use some CSS magic,
				storeName: store.name,
				maxStoreRate: store.max,
				newCustomersOnly: store.newCustomersOnly
			});
			document.body.insertBefore(template, document.body.firstChild);
			self.bindConfirmationDialogEvents();
		});
	}

	/**
	 * @param cause The cause object for the currently logged-in user.
	 * @param store The store object for the currently tracked store.
	 */
	injectThankYou(cause, store) {
		var self = this;
		whenDocumentReady(() => {
			if(self.isThankYouInjected()) { // Prevent duplicate injections of the same dialog.
				return;
			}

			// There exists a point where we activate non-active tabs, in this situation there may be a confirmation dialog visible, so we need to remove it.
			if(self.isConfirmationInjected()) {
				console.log('Found an existing confirmation dialog, removing all dialogs.');
				self.removeAllDialogs();
			}

			var model = {
				causeName: (cause && cause.name) || null,
				maxStoreRate: store.max,
				newCustomersOnly: store.newCustomersOnly
			};
			var template = thankYouTemplate(model);
			document.body.insertBefore(template, document.body.firstChild);
			self.bindThankYouEventHandlers();

			// Hide the message after a delay
			let delayMs = this.autoInjectedThankYou ? 60000 : Config.thankYouDialogVisibleMs;
			setTimeout(function() {
				self.hideThankYou();
			}, delayMs);
		});
	}

	hideThankYou() {
		document.querySelector(THANK_YOU_SELECTOR).classList.add('is-hidden');
	}

	removeConfirmationDialog() {
		removeNode(CONFIRMATION_DIALOG_SELECTOR);
	}

	// User doesn't want to activate the donation reminder
	onDecline() {
		this.sendMessageToBackground(new Message(MessageSubject.RETAILER_REJECT))
	}

	// User confirmed they want to activate the donation reminder
	onConfirmation() {
		this.sendMessageToBackground(new Message(MessageSubject.RETAILER_CONFIRM, {
			extensionSource: ExtensionSource.RETAILER
		}));
	}

	sendMessageToBackground(message) {
		runtime.sendMessage(message);
	}

	/**
	 * 	Helpers to determine which things we've already injected into the page.
	 */

	// Have ANY of the dialogs been injected?
	isInjected() {
		return document.querySelector(INJECTED_SELECTOR) !== null;
	}

	// Has this specific dialog been injected?
	isThankYouInjected() {
		return document.querySelector(THANK_YOU_SELECTOR) !== null;
	}

	// Has this specific dialog been injected?
	isConfirmationInjected() {
		return document.querySelector(CONFIRMATION_DIALOG_SELECTOR) !== null;
	}
}

/**
 * Given a selector, will remove the first match of that selector from the DOM.
 */
function removeNode(selector) {
	var el = document.querySelector(selector);
	if(el === null) return;
	var parent = el.parentNode;

	if(parent !== null) {
		parent.removeChild(el);
	}
}

contentScriptInitialise(RetailerContentScript, isRetailerPage(window.location.href));