var getBrowserName = require('./shared/get-browser-name.js');
var runtime = require('./web-ext/runtime.js');

 // Map of browser name => auth header
 var BROWSER_AUTH_HEADERS = {
 	'Chrome': 'R0FZTF9CUk9XU0VSX0VYVF9DSFJPTUU6MnZMTG4qSkpyX3ZEOT1hKg==',
 	'Firefox': 'R0FZTF9CUk9XU0VSX0VYVF9GSVJFRk9YOnRBM3lOWEJQWmR1YyVHTG0=',
 	'Edge': 'R0FZTF9CUk9XU0VSX0VYVF9FREdFOmZ2Qmg4VHdMUDRHelNTQFI=',
 	'Safari': 'R0FZTF9CUk9XU0VSX0VYVF9TQUZBUkk6LTZyRHZBJGREeHFHOW45Pw==',
 };

module.exports = {
	// Auth header used for API calls
	apiAuthHeader: function() {
		var browserName = getBrowserName();
		var authHeader = BROWSER_AUTH_HEADERS[browserName];
		if(!authHeader) {
			throw new Error('Cannot find auth header for ' + browserName);
		}
		return 'Basic ' + authHeader;
	},
	extensionId: function(){
		// Edge actually has this property on their window object, so we need
		// to check if 'runtime' property exists to see if it's actually Chrome...
		if(typeof chrome !== 'undefined' && chrome.runtime) {
			return chrome.runtime.id;
		}

		// Safari doesn't have it's own extension ID, so we have to create a fake one.
		if(typeof safari !== 'undefined') {
			return 'synthetic';
		}

		return browser.runtime.id
	},

	// Schemes that shouldn't be processed by the extension
	ignoredSchemes: [
		'about',
		'chrome',
		'chrome-extension',
		'file',
		'view-source',
		'wyciwyg',
		'favorites'
	],

	// URLs that shouldn't be processed by the extension
	ignoredUrls: '(?:shareasale.com|paypal.(?:co.uk|com)|successfultogether.co.uk|tradedoubler.com|clkuk.pvnsolutions.com|paidonresults.net|linksynergy.com|doubleclick.net|affiliatefuture.com|omguk.com|healthtrader.com|digitalriver.com|veinteractive.com|track.webgains.com|jdoqocy.com)',

	// Where the instructions live for changing your browser extension permissions.
	permissionsUrl: 'https://help.giveasyoulive.com/hc/en-us/articles/360017448352-New-Chrome-update-Donation-Reminder',

	// How long to wait (ms) before processing the navigation committed event.
	processingDelayMs: 1000,

	// How long to wait (ms) before cancelling pending HTTP requests
	requestTimeoutMs: 7000,

	// Sentry DSN (Data Source Name) - https://docs.sentry.io/quickstart/#configure-the-dsn
	sentryDsn: 'https://7a19d517524a4954bb10e4bc21e4a4a9@sentry.io/1270266',

	// How long to show the thank you dialog (ms) before hiding.
	thankYouDialogVisibleMs: 7000,

	// The webpage a user is sent to after uninstalling
	uninstallUrl: 'https://r1.dotmailer-surveys.com/7e21zb6c-623a1ufe',

	// Function that returns the currently installed version of the Donation Reminder.
	version: function() {
		return runtime.getManifest().version;
	}
};