/**
 *	WebExtensions runtime API polyfill.
 *
 * 	@author Daniel Moffat
 */
var runtime;

// Firefox / Chrome
if('undefined' !== typeof chrome && chrome.runtime) {
	runtime = chrome.runtime;
}

// Microsoft Edge
if('undefined' !== typeof browser && browser.runtime) {
	runtime = browser.runtime;
}

module.exports = runtime;